import React, {useState, useEffect} from 'react';

import API from '../api';
import {message, Row, Input, Table, Col, Modal, Button} from "antd";
import {StepBackwardOutlined, StepForwardOutlined} from '@ant-design/icons';
import moment from "moment";

const columns = [
  {
    title: 'Exam',
    dataIndex: 'examId',
    key: 'examId',
  },
  {
    title: 'Student Name',
    dataIndex: 'studentName',
    key: 'studentName',
  },
  {
    title: 'Time',
    dataIndex: 'time',
    key: 'time',
    render: (data) => <div>{moment(parseInt(data)).format('YYYY-MM-DD HH:mm:ss')}</div>,
  },
  {
    title: 'Filename',
    dataIndex: 'fileName',
    key: 'fileName',
  },
];

export default () => {
  const [allFiles, setAllFiles] = useState([]);
  const [filter, setFilter] = useState('');

  const [selected, setSelected] = useState(0);
  const [imageViewer, showImageViewer] = useState(false);

  useEffect(() => {
    (async () => {
      const [err, data] = await API.get('all-images');
      if (err) {
        message.error('Fail to load images. try again please');
      } else {
        setAllFiles(data.filenames.map(name => {
          const x = name.split('_');
          return {
            examId: x[0],
            studentName: x[1],
            time: x[2] && x[2].split('.')[0],
            fileName: name,
          }
        }));
        setFilter('')
      }
    })();
  }, []);

  const filteredFiles = allFiles.filter(value => value.fileName.includes(filter));

  return <div>
    <Row>
      <Col span={24}>
        <Input value={filter} onChange={(e) => setFilter(e.target.value)} placeholder="Filter by file name"/>
      </Col>
    </Row>
    <Row>
      <Col span={24}>
        <Table
          dataSource={filteredFiles}
          columns={columns}
         onRow={(record, rowIndex) => ({
            onClick: () => {
              setSelected(rowIndex);
              showImageViewer(true);
            },
          })}
         pagination={{ position: ['topRight'] }}
          scroll={{ y: 750 }}
        />
      </Col>
    </Row>

    <Modal
      title={`${filteredFiles[selected] && moment(parseInt(filteredFiles[selected].time)).format('YYYY-MM-DD HH:mm:ss')} | ${selected + 1}/${filteredFiles.length}`}
      visible={imageViewer}
      onOk={() => showImageViewer(false)}
      onCancel={() => showImageViewer(false)}
      footer={<>
        <Button type="primary" icon={<StepBackwardOutlined/>}
                onClick={() => {
                    if (selected !== 0) {
                      setSelected(selected - 1);
                    }
                  }
                }
        />
        <Button type="primary" icon={<StepForwardOutlined/>} onClick={() => {
          if (selected !== filteredFiles.length - 1) {
            setSelected(selected + 1);
          }
        }
        }/>
        <Button onClick={() => showImageViewer(false)}>Cancel</Button>
      </>}
    >
      <img style={{width: '100%' }} src={`/images/${filteredFiles[selected] && filteredFiles[selected].fileName}`}/>
    </Modal>
  </div>
}


