import axios from 'axios';
const to = (promise) => promise.then((data) => [null, data]).catch((err) => [err, null]);
const API_ROOT = '/'; // root of the api url

const responseBody = (res) => res.data; // an arrow function to takes only the data value from the response object
const errBody = (res) => {
  throw res.response.data;
};

// general 4 types of requests
export default {
  get: (url, headers = {}) =>
    to(
      axios
        .get(`${API_ROOT}${url}`, { headers })
        .then(responseBody)
        .catch(errBody),
    ),
  post: (url, body, headers = {}) =>
    to(
      axios
        .post(`${API_ROOT}${url}`, body, { headers })
        .then(responseBody)
        .catch(errBody),
    ),
  put: (url, body, headers = {}) =>
    to(
      axios
        .put(`${API_ROOT}${url}`, body, { headers })
        .then(responseBody)
        .catch(errBody),
    ),
  patch: (url, body, headers = {}) =>
    to(
      axios
        .patch(`${API_ROOT}${url}`, body, { headers })
        .then(responseBody)
        .catch(errBody),
    ),
  del: (url, headers = {}) =>
    to(
      axios
        .delete(`${API_ROOT}${url}`, { headers })
        .then(responseBody)
        .catch(errBody),
    ),
};

export { to };
