import React, {useState} from 'react';
import {Layout, Menu} from 'antd';
import {
  DesktopOutlined,
  PieChartOutlined,
} from '@ant-design/icons';
import Exams from "./exams";
import Images from "./images";

const { Sider, Header, Content, Footer} = Layout;

export default ({page, setPage}) => {
  const [collapsed, setCollapsed] = useState(false);

  const onCollapse = collapsed => {
    setCollapsed(collapsed);
  };

  return <Layout style={{ minHeight: '100vh' }}>
    <Sider collapsible collapsed={collapsed} onCollapse={onCollapse}>
      <div className="logo" style={{height: 60}} />
      <Menu theme="dark" defaultSelectedKeys={['1']} mode="inline">
        <Menu.Item key="1" icon={<PieChartOutlined />} onClick={() => setPage('exam')}>
          Exams
        </Menu.Item>
        <Menu.Item key="2" icon={<DesktopOutlined />} onClick={() => setPage('images')}>
          Images
        </Menu.Item>
      </Menu>
    </Sider>
    <Layout className="site-layout">
      <Header className="site-layout-background" style={{ padding: 0 }} />
      <Content style={{ margin: '0 16px' }}>
        {page === 'exam' && <Exams />}
        {page === 'exam2' && <Exams />}
        {page === 'images' && <Images />}
      </Content>
      <Footer style={{ textAlign: 'center' }}>_______</Footer>
    </Layout>
  </Layout>
}
