import React, {useEffect, useState} from "react";
import {Button, Table, Row, Col, Drawer, Form, Input, DatePicker, message, Slider} from 'antd';
import {DeleteOutlined} from '@ant-design/icons';
import moment from 'moment';
import API from '../api';

const {RangePicker} = DatePicker;

const layout = {
  labelCol: {span: 8},
  wrapperCol: {span: 16},
};
const tailLayout = {
  wrapperCol: {offset: 8, span: 16},
};

const getColumns = (onDelete) => ([
  {
    title: 'Exam Name',
    dataIndex: 'name',
    key: 'name',
  },
  {
    title: 'Exam Id',
    dataIndex: 'examId',
    key: 'examId',
  },
  {
    title: 'Start At',
    dataIndex: 'startAt',
    key: 'startAt',
    render: (data) => <div>{moment(data).format('YYYY-MM-DD HH:mm')}</div>,
  },
  {
    title: 'End At',
    dataIndex: 'endAt',
    key: 'endAt',
    render: (data) => <div>{moment(data).format('YYYY-MM-DD HH:mm')}</div>,
  },
  {
    title: '',
    key: 'actions',
    render: (tags, row) => (
      <>
        <Button type="danger" icon={<DeleteOutlined/>} onClick={async () => {
          const [err, data] = await API.del('delete-exam/' + row._id);
          if (err) {
            message.error('Failed to delete');
          } else {
            message.success('Successfully deleted');
            onDelete(row._id);
          }
        }}/>
        {/*<Button type="" icon={<EditOutlined />} />*/}
      </>
    ),
  }
]);

const AddExamForm = ({onSuccess}) => {
  const onFinish = async (values) => {
    let rate = values.rate || [10, 20];
    let cnf;
    try {
      cnf = JSON.parse(values.captureConfig);
    } catch (e) {
      cnf = {
        width: 320,
        height: 240,
        image_format: 'jpeg',
        jpeg_quality: 90,
      }
    }
    const [err, data] = await API.post('create-exam', {
      name: values.name,
      examId: values.examId,
      startAt: values.range[0].valueOf(),
      endAt: values.range[1].valueOf(),
      captureConfig: cnf,
      captureRate: {
        min: rate[0],
        max: rate[1],
      }
    });
    if (err) {
      message.error('Something went wrong');
    } else {
      message.success('Exam entry added');
      onSuccess(data.newExam)
    }
  }

  const onFinishFailed = (errors) => {

  }

  return <Form
    {...layout}
    name="basic"
    initialValues={{remember: true}}
    onFinish={onFinish}
    onFinishFailed={onFinishFailed}
  >
    <Form.Item
      label="Exam Name"
      name="name"
      rules={[{required: true, message: 'Please input exam name!'}]}
    >
      <Input/>
    </Form.Item>
    <Form.Item
      label="Exam Id"
      name="examId"
      rules={[{required: true, message: 'Please input exam ID!'}]}
    >
      <Input/>
    </Form.Item>

    <Form.Item
      label="Enable Time Range"
      name="range"
      rules={[{required: true, message: 'Please add time rage'}]}
    >
      <RangePicker
        showTime={{format: 'HH:mm'}}
        format="YYYY-MM-DD HH:mm"
      />
    </Form.Item>

    <Form.Item label="Capture Rate (min)" name="rate">
      <Slider range defaultValue={[10, 20]} />
    </Form.Item>

    <Form.Item
      label="Capturing Configs"
      name="captureConfig"
    >
      <Input.TextArea/>
    </Form.Item>

    <Form.Item label="Example">
      <code>
        {'{'} <br/>
        "width": 320, <br/>
        "height": 240, <br/>
        "image_format": "jpeg", <br/>
        "jpeg_quality": 90 <br/>
        {'}'}
      </code>
    </Form.Item>

    <Form.Item {...tailLayout}>
      <Button type="primary" htmlType="submit">
        Submit
      </Button>
    </Form.Item>
  </Form>
}

export default () => {
  const [loading, setLoading] = useState(false);
  const [data, setData] = useState([]);
  const [addDrawer, showAddDrawer] = useState(false);

  useEffect(() => {
    (async () => {
      setLoading(true);
      const [err, data] = await API.get('get-exams');
      if (err) {
        message.error('Exam data loading failed. try to reload');
      } else {
        if (data.success) {
          setData(data.data);
        } else {
          message.error('Exam data loading failed. try to reload');
        }
      }
      setLoading(false);
    })();
  }, [])

  return <div>
    <Row>
      <Col span={24}>
        <Button type="primary" style={{float: 'left', margin: 5}} onClick={() => showAddDrawer(true)}>Add New</Button>
      </Col>
    </Row>
    <Row>
      <Col span={24}>
        <Table columns={getColumns((rowId) => {
          setData(data.filter(row => row._id !== rowId));
        })} dataSource={data} loading={loading}/>
      </Col>
    </Row>

    <Drawer
      title="Exam"
      placement="right"
      width={600}
      onClose={() => showAddDrawer(false)}
      visible={addDrawer}
    >
      <AddExamForm onSuccess={(newExam) => {
        showAddDrawer(false);
        setData([...data, newExam])
      }}/>
    </Drawer>
  </div>;
}
