import React, {useState} from 'react';
import logo from './logo.svg';

import 'antd/dist/antd.css';
import './App.css';

import Login from './components/login';
import Layout from './components/layout';

function App() {
  const [page, setPage] = useState('exam');
  const ascot = parseInt(localStorage.getItem('ascot'));

  let login;
  if (isNaN(ascot)) {
    login = false;
  } else {
    login = ascot > Date.now();
  }


  return (
    <div className="App">
      {!login && <Login setPage={setPage} />}
      {login && <Layout page={page} setPage={setPage} />}
    </div>
  );
}

export default App;
